            </div>
            <div class="appointmentDetail">
                <div class="appointmentTitle"></div>
                <div class="appointmentContent">
                    <div v-if="!serviceState" style="font-size: 28rem / @rootFontSize"></div>
                    <div v-if="serviceState" class="serveTime">
