                    </div>
                    <div v-if="serviceState" class="serveLocation">
                        <div style="width: 100rem / @rootFontSize;display: flex;justify-content: center;align-items: center;width: 70px">
                            
                        </div>
                        <span style="font-size: 28rem / @rootFontSize">{{serviceAddress}}  {{serviceCoordinates}}</span>
                    </div>
                </div>
            </div>
            <div class="expertIntroduction">
                <div class="expertTitle"></div>
                <div class="expertDetail">
                    <div v-if="!serviceState" style="font-size: 28rem / @rootFontSize"></div>
                    <div v-if="serviceState" class="rich-text-wrp">
                        <div class="expertName">
                            <div>{{expertName}}</div>
                            <span v-if="expertState==1">()</span>
                            <span v-if="expertState==2">()</span>
                            <span v-if="expertState==3">()</span>
                        </div>
                        <div v-html="expertDetails"></div>
                    </div>
                </div>
            </div>
            <div class="coupons">
                <div class="left">
                    <img src="../img/conpon.png">
                    <span>{{orderSalesCode}}</span>
                </div>
                <div class="right">
                    <div v-if="serviceState==1||!serviceState" class="writeOffStutas"></div>
                    <div v-if="serviceState==2" class="writeOffStutas"></div>
                </div>
            </div>
            <div class="viewImg">

                <div class="title">
                    <img class="photo" src="../img/photo.png">
                    <span style="font-size: 28rem / @rootFontSize"></span>
                </div>
                <div class="zhiYuanImg">
                    <div v-if="voluntaryFormUrl">
                        <viewer :images="images">
                            <v-touch class="imgDiv" v-for="src in images" :key="src">
                                <img :src="src" alt="">
                            </v-touch>

                        </viewer>
                        <!--<a :href="voluntaryFormUrl"></a>-->
                    </div>

                    <div v-if="!voluntaryFormUrl" class="voluntaryFormUrl"></div>
