<script>
    import request from '../../../../static/js/request'
    import result from '../../../../static/js/result'
    import Vue from 'vue'
    import 'viewerjs/dist/viewer.css'
    import Viewer from 'v-viewer'
    import getParam from '../../../../static/js/getParam';
    import wxInfo from '../../../../static/js/wxPublicInfo'
    Vue.use(Viewer, {
        defaultOptions: {
            zIndex: 1,
            inline: false,
            button: true,
            navbar: false,
            title: false,
            toolbar: false,
            tooltip: true,
            movable: true,
            zoomable: true,
            rotatable: false,
            scalable: false,
            transition: true,
            fullscreen: true,
            keyboard: false,
            loop: false,
        }
    })
    export default {
        data() {
            return {
                orderId: 0,
                orderUserName: '',
                orderProductName: '',
                isNo: true,
                expertId: 0,
                expertName: '',
                serviceTime: '',
                serviceRegion: '',
                serviceAddress: '',
                serviceCoordinates: '',
                serviceWoCode: '',
                serviceWoTime: '',
                serviceState: 0,
                voluntaryFormUrl: '',
                orderState: 0,
                expertDetails: '',
                expertState: 0,
                orderSalesCode: '',
                images: [],
                orderCode: '',
                orderProductPrice:0
            }
        },
        mounted() {

        },
        created() {
            /**/
            let REDIRECT_URI = encodeURIComponent(this.$host+'/myInfo.html#/payRecordDetail?orderId='+this.$route.query.orderId)
            let url = location.href
            /**/
            if (!getParam('code')) {
                console.log(REDIRECT_URI, wxInfo.wxAppId)
                location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${wxInfo.wxAppId}&redirect_uri=${REDIRECT_URI}&response_type=code&scope=snsapi_base&state=STATE#wechat_redirect`
            }
            let that = this
            /* that.setData({
                 orderId:options.orderId
             })*/
            that.getOrderList();
        },
        methods: {
            /**
             * 
             * **/
            goBack() {
                this.$router.go(-2)
            },
            /**
             * 
             * **/
            async getOrderList() {

                let params = {
                    access_token: this.$store.state.token,
                    orderId: this.$route.query.orderId
                }

                let getUserInfo = await request('/api/token/order/selectOrderInfoById',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let info = result(getUserInfo)
                // loading.hide()
                if (!info) return;
                this.orderUserName = info.orderUserName;
                this.orderProductName = info.orderProductName;
                this.orderProductPrice = info.orderProductPrice
                this.orderSalesCode = info.orderSalesCode
                this.orderCode = info.orderCode;
                this.orderState = info.orderState
                this.getService(info.orderId)
            },
            /**
             * 
             * **/
            async getService() {

                let params = {
                    access_token: this.$store.state.token,
                    orderId: this.$route.query.orderId
                }

                let getUserInfo = await request('/api/token/service/list',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let info = result(getUserInfo)
                // loading.hide()
                if (!info) return;
                if (info.length > 0) {
                    this.expertId = info[0].expertId
                    this.expertName = info[0].expertName
                    this.serviceTime = info[0].serviceTime
                    this.serviceRegion = info[0].serviceRegion
                    this.serviceAddress = info[0].serviceAddress
                    this.serviceCoordinates = info[0].serviceCoordinates
                    this.serviceWoCode = info[0].serviceWoCode
                    this.serviceState = info[0].serviceState
                    this.serviceWoTime = info[0].serviceWoTime
                    this.voluntaryFormUrl = info[0].voluntaryFormUrl
                    this.serviceState = info[0].serviceState
                    this.images.push(info[0].voluntaryFormUrl)
                    if (info[0].expertId) {
                        this.getExpert(info[0].expertId)
                    }
                }

                /*this.orderUserName= info.orderUserName,
                    this.orderProductName=info.orderProductName,*/
                console.log(info)

            },
            /**
             * 
             * **/
            async getExpert(expertId) {

                let params = {
                    access_token: this.$store.state.token,
                    expertId: expertId,
                }

                let getUserInfo = await request('/api/token/user/selectUserExpertById',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let info = result(getUserInfo)
                // loading.hide()
                if (!info) return;
                console.log(info)
                this.expertDetails = info.expertDetails
                this.expertState = info.expertState
            },
            /**
             * 
             * **/
            async pay() {
                let that = this;
                let loading = weui.loading('loading', {
                    className: 'custom-classname'
                });
                let params1 = {
                    attach: '',
                    body: that.orderProductName,
                    orderId: this.$route.query.orderId,
                    total_fee: Number(this.orderProductPrice * 100).toFixed(),
                    code: getParam('code')
                }
                let payInfo = await request('/api/open/wxPay/webPay',
                    'post', params1, 'application/x-www-form-urlencoded')
                // loading.hide()

                let res = result(payInfo);
                console.log(payInfo)
                let info = JSON.parse(payInfo.msg)
                // loading.hide()
                if (!res) return
                console.log(info);
                loading.hide()
                this.$nextTick(() => {
                    this.pullUpPay(info.appId, info.timeStamp, info.nonceStr, info.signType, info.paySign, info.package, this.$route.query.orderId)
                })
            },
            /**
             *  
             * */
            async pullUpPay(appId, timeStamp, nonceStr, signType, paySign, payId, orderId) {
                let that = this
                console.log(appId, timeStamp, nonceStr, signType, paySign, payId)

                function onBridgeReady() {
                    WeixinJSBridge.invoke(
                        'getBrandWCPayRequest', {
                            "appId": appId,   //
                            "timeStamp": timeStamp,     //1970
                            "nonceStr": nonceStr, //
                            "package": payId,  // prepay_id 
                            "signType": signType,     //
                            "paySign": paySign //
                        },
                        async function (res) {
                            if (res.err_msg == "get_brand_wcpay_request:ok") {

                                let params2 = {
                                    access_token: this.$store.state.token,
                                    orderId: orderId
                                }
                                let orderInfo = await request('/api/token/order/selectOrderInfoById',
                                    'post', params2, 'application/x-www-form-urlencoded')
                                // loading.hide()
                                let res = result(orderInfo)
                                if (!res) return;
                                console.log('', orderInfo)
                                let params = {
                                    access_token: this.$store.state.token,
                                    orderCode: orderInfo.data.orderCode,
                                    orderId: orderId,
                                    agentId: orderInfo.data.orderAgentId?orderInfo.data.orderAgentId:'',
                                    agentName: orderInfo.data.orderAgentName?orderInfo.data.orderAgentName:'',
                                    expertId: orderInfo.data.orderExpertId ? orderInfo.data.orderExpertId : '',
                                    expertName: orderInfo.data.orderExpertName ? orderInfo.data.orderExpertName : '',
                                    studentName: that.studentName
                                }
                                if(that.$route.query.activityId){
                                    params.activityId = that.$route.query.activityId
                                }
                                let addServeInfo = await request('/api/token/service/add',
                                    'post', params, 'application/x-www-form-urlencoded')
                                let info1 = result(addServeInfo)
                                console.log(addServeInfo)
                                if (!info1) return;
                                weui.toast('');
                                that.$nextTick(() => {
                                    location.reload()
                                })
                            } else {
                                weui.toast('');

                            }
                        }
                    )
                }

                // 
                if (typeof WeixinJSBridge == "undefined") {
                    if (document.addEventListener) {
                        document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
                    } else if (document.attachEvent) {
                        document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
                        document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
                    }
                } else {
                    onBridgeReady();
                }
                ;
            },
        }
    }
</script>

